import React, { useEffect } from 'react'; // Import the 'React' module

import { yupResolver } from '@hookform/resolvers/yup';
// import { requestEmailVerification } from 'api/user_api';
import Button from 'components/ui/atoms/Button/Button';
import CheckBox from 'components/ui/atoms/CheckBox/CheckBox';
import Input from 'components/ui/atoms/Input/Input';
import InputGroup from 'components/ui/molecules/InputGroup/InputGroup';
import { RegisterSchema } from 'components/validations/validations';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import theme from 'styles/theme';
import { Container, Content } from './LoginTemplate';
import {
	checkEmail,
	checkNickname,
	confirmEmailVerification,
	requestEmailVerification,
	signup,
	// signup,
} from 'api/User';
import useTimer from 'hooks/useTimer';
import { secondsToMMSS } from 'hooks/util';
import useRouter from 'hooks/useRouter';

/**
 *
 * TODO :
 * onChange      - '   '  , disabled  
 *   + 
 * onSubmit   
 * input props 
 */

const SignupTemplate = () => {
	const {
		register,
		getValues,
		handleSubmit,
		formState: { errors },
		setError,
		trigger,
	} = useForm({
		resolver: yupResolver(RegisterSchema),
		mode: 'onChange',
	});

	const { moveLoginPage } = useRouter();
	const [emailDisabled, setEmailDisabled] = useState(false);
	const [emailSuccess, setEmailSuccess] = useState(false);
	const [codeDisabled, setCodeDisabled] = useState(false);
	const [codeSuccess, setCodeSuccess] = useState(false);
	const [nicknameSuccess, setNicknameSuccess] = useState(false);
	const [key, setKey] = useState('');

	const { start, seconds, isRunning, stop } = useTimer(180);

	// timer start
	useEffect(() => {
		if (emailDisabled) {
			start();
		}
	}, [start]);

	const handleEmailVerification = async () => {
		stop();
		const isValid = await trigger('email');
		if (!isValid) {
			return;
		}
		setEmailDisabled(true);
		const email = getValues('email');
		try {
			const { exist } = await checkEmail(email);
			if (exist) {
				setEmailDisabled(false);
				setError('email', {
					type: 'exist',
					message: '   .',
				});
				return;
			}

			const { key } = await requestEmailVerification(email);
			start();
			setKey(key);
			setEmailSuccess(true);
			setCodeDisabled(false);
			setCodeSuccess(false);
		} catch (err) {
			setEmailDisabled(false);
		}
	};

	const handleCodeVerification = async () => {
		const isValid = await trigger('code');
		if (!isValid) {
			return;
		}

		if (!isRunning) {
			setError('code', {
				type: 'custom',
				message: '  .  .',
			});
		}

		const email = getValues('email');
		const code = getValues('code') ?? 0;
		try {
			setCodeDisabled(true);
			await confirmEmailVerification(email, code.toString(), key);
			setCodeSuccess(true);
			stop();
		} catch (err) {
			setCodeDisabled(false);
			setCodeSuccess(false);
			setError('code', {
				type: 'custom',
				message: '   .  .',
			});
		}
	};

	//   
	const handleCheckNickname = async () => {
		const nickname = getValues('nickname');
		try {
			const { exist } = await checkNickname(nickname);
			if (exist) {
				throw new Error(`${nickname} exist`);
			}
			setNicknameSuccess(true);
		} catch (err) {
			console.log(err);
			setError('nickname', {
				type: 'exist',
				message: '  .',
			});
			setNicknameSuccess(false);
		}
	};

	const onSubmit = async (data: any) => {
		try {
			console.log(data);
			await signup({
				user_email: data.email,
				user_password: data.password,
				user_nickname: data.nickname,
				key,
			});

			moveLoginPage();
		} catch (err) {
			console.log(err);
		}
	};

	return (
		<Container height="800px" onSubmit={handleSubmit(onSubmit)}>
			<Content>
				<Wrapper>
					<InputGroup>
						<Input
							width="315px"
							placeholder="()"
							{...register('email')}
							error={errors.email?.message}
						/>
						<Button
							label=""
							size="small"
							variant="primary"
							border="none"
							disable={emailDisabled}
							onClick={handleEmailVerification}
							type="button"
						/>
					</InputGroup>
					{emailSuccess && <SuccessText>  .</SuccessText>}
				</Wrapper>
				<Wrapper>
					<InputGroup>
						<Input
							width="315px"
							placeholder=""
							time={isRunning ? secondsToMMSS(seconds) : ''}
							{...register('code')}
							error={
								errors.code?.type == 'custom'
									? errors.code?.message
									: errors.code?.message
									? '  .'
									: ''
							}
						/>
						<Button
							label=" "
							size="small"
							variant="primary"
							border="none"
							disable={codeDisabled}
							onClick={handleCodeVerification}
							type="button"
						/>
					</InputGroup>
					{codeSuccess && (
						<SuccessText>   .</SuccessText>
					)}
				</Wrapper>
				<Wrapper>
					<InputGroup>
						<Input
							width="315px"
							placeholder=""
							{...register('nickname')}
							error={errors.nickname?.message}
						/>
						<Button
							label=" "
							size="small"
							variant="primary"
							border="none"
							onClick={handleCheckNickname}
							type="button"
						/>
					</InputGroup>
					{nicknameSuccess && (
						<SuccessText>   .</SuccessText>
					)}
				</Wrapper>
				<Wrapper>
					<Input
						width="100%"
						placeholder="(8  , ,  )"
						icon={true}
						type="password"
						{...register('password')}
						error={errors.password?.message}
					/>
				</Wrapper>
				<Wrapper>
					<Input
						width="100%"
						placeholder=" "
						type="password"
						{...register('passwordConfirm')}
						error={errors.passwordConfirm?.message}
					/>
				</Wrapper>
				<Wrapper>
					<Checks>
						<span>
							<Link to="#">()    </Link> .
						</span>
						<CheckBox variant="primary" />
					</Checks>
				</Wrapper>
				<ButtonBox>
					<Button
						label=""
						variant="primary"
						border="none"
						size="large"
						disable={false}
						type="submit"
					/>
				</ButtonBox>
			</Content>
		</Container>
	);
};

export default SignupTemplate;

const Wrapper = styled.div`
	padding: 20px 0;
	height: 50px;
	box-sizing: none;
`;

const Checks = styled.div`
	padding-top: 50px;
	display: flex;
	justify-content: space-between;
	a {
		text-decoration: none;
		color: ${theme.PALETTE.primary[100]};
	}
`;

export const ButtonBox = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 30px;
`;

const SuccessText = styled.p`
	color: #009900;
	font-size: ${theme.FONT_SIZE.small};
	font-family: 'Roboto Mono';
`;
