import { yupResolver } from '@hookform/resolvers/yup';
import Button from 'components/ui/atoms/Button/Button';
import Input from 'components/ui/atoms/Input/Input';
import InputGroup from 'components/ui/molecules/InputGroup/InputGroup';
import { PasswordReset } from 'components/validations/validations';
import React from 'react';
import { useForm } from 'react-hook-form';
import styled from 'styled-components';
import { Container, Content, Wrapper } from '../AuthPageTemplate/LoginTemplate';
import { ButtonBox } from '../AuthPageTemplate/SignupTemplate';

const RePasswordTemplate = () => {
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm({
		resolver: yupResolver(PasswordReset),
		mode: 'onChange',
	});

	const onSubmit = (data: any) => {
		console.log(data);
	};

	const data = {
		time: '3:00',
	};
	return (
		<Container height="650px" onSubmit={handleSubmit(onSubmit)}>
			<Content>
				<Wrapper>
					<InputGroup>
						<Input width="315px" placeholder="()" value={''} />
						<Button
							size="small"
							variant="primary"
							label=""
							border="none"
							disable={false}
						/>
					</InputGroup>
				</Wrapper>
				<Wrapper>
					<InputGroup>
						<Input width="315px" placeholder="" time={data.time} />
						<Button
							size="small"
							variant="primary"
							label=" "
							border="none"
							disable={false}
						/>
					</InputGroup>
				</Wrapper>
				<Wrapper>
					<Input
						width="100%"
						placeholder="(8  , ,  )"
						icon={true}
						type="password"
						{...register('password')}
						error={errors.password?.message}
					/>
				</Wrapper>
				<Wrapper>
					<Input
						width="100%"
						placeholder=" "
						type="password"
						{...register('passwordConfirm')}
						error={errors.passwordConfirm?.message}
					/>
				</Wrapper>
				<Wrapper>
					<Space />
				</Wrapper>
				<ButtonBox>
					<Button
						label=" "
						size="large"
						variant="primary"
						border="none"
					/>
				</ButtonBox>
			</Content>
		</Container>
	);
};

export default RePasswordTemplate;

const Space = styled.div`
	height: 50px;
`;
