export const RegisterSchema = yup.object().shape({
	email: yup
		.string()
		.required('  .')
		.email('   .')
		.matches(/\S+@\S+\.\S+/, '  '),
	// .test(
	// 	'   .',
	// 	'   .',
	// 	async (value, values) => {
	// 		const verified = await verifyEmail(value as string, values);
	// 		return verified as boolean;
	// 	},
	// ),
	code: yup.number(),
	nickname: yup
		.string()
		.required(' .')
		.min(4, '  4  .')
		.max(12, '  12  .')
		.matches(
			/^(?=.*[a-zA-Z---])(?=.*\d)[a-zA-Z---\d]+$/,
			', ,    4  12   .',
		)
		.test(
			'no-only-numbers',
			'     .',
			value => !/^\d+$/.test(value),
		),
	password: yup
		.string()
		.min(8, '  8  .')
		.matches(
			/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*])[a-zA-Z\d!@#$%^&*]+$/,
			' , ,    .',
		),
	passwordConfirm: yup
		.string()
		.required(' .')
		.oneOf([yup.ref('password')], ' .'),
});

export const LoginSchema = yup.object().shape({
	email: yup
		.string()
		.required('  .')
		.email('   .'),
	password: yup.string().required(' .'),
});

export const PasswordReset = yup.object().shape({
	password: yup
		.string()
		.min(8, '  8  .')
		.matches(
			/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*])[a-zA-Z\d!@#$%^&*]+$/,
			' , ,    .',
		),
	passwordConfirm: yup
		.string()
		.required(' .')
		.oneOf([yup.ref('password')], ' .'),
});
